.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

body {
  font-family: "DM Serif Display", serif;
  font-weight: 700;
}

h1 {
  font-size: 2rem;
}

p {
  font-family: "Karla", sans-serif;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 1.8;
}

.header {
  padding: 2rem 1.5rem;
  display: flex;
  justify-content: space-between;
}
.header__logo {
  width: 15rem;
  object-fit: contain;
}
.header__navIcon {
  border: none;
  background-color: transparent;
}
@media (min-width: 768px) {
  .header__navIcon {
    display: none;
  }
}
.header__navIcon:hover {
  cursor: pointer;
}

.navigation {
  display: none;
  text-transform: uppercase;
  align-items: center;
  font-size: 1.6rem;
  gap: 2rem;
  font-family: "Karla", sans-serif;
}
@media (min-width: 768px) {
  .navigation {
    display: flex;
  }
}
@media (max-width: 767px) {
  .navigation.show {
    display: flex;
    position: absolute;
    width: 100%;
    top: 7.5rem;
    left: 0;
    bottom: 0;
    flex-direction: column;
    align-items: center;
    background-image: url("../img/bg-pattern-mobile-nav.svg"), linear-gradient(to bottom, #2b272f 0% 100%);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: bottom center;
    z-index: 10;
  }
}
.navigation__link {
  text-decoration: none;
  text-align: center;
  color: #fafafa;
}
@media (max-width: 767px) {
  .navigation__link {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
    font-size: 1.8rem;
  }
  .navigation__link:first-of-type {
    padding-top: 5rem;
  }
}
@media (max-width: 767px) and (min-width: 768px) {
  .navigation__link:first-of-type {
    padding-top: 2rem;
  }
}
@media (min-width: 768px) {
  .navigation__link {
    color: #837d87;
  }
  .navigation__link:hover {
    color: #2d2640;
  }
}
.navigation__link--button {
  border: 0.1rem solid #fafafa;
  color: #fafafa;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  width: 85%;
}
@media (max-width: 767px) {
  .navigation__link--button {
    margin-top: 0.5rem;
    padding: 1.5rem 2rem;
  }
}
@media (min-width: 768px) {
  .navigation__link--button {
    margin-top: 0;
    padding: 1rem 2.5rem;
  }
}
.navigation__link--button:hover {
  background-color: #fafafa;
  color: #2b272f;
}
@media (min-width: 768px) {
  .navigation__link--button {
    width: unset;
    border: 0.1rem solid #2b272f;
    color: #2b272f;
    text-decoration: none;
    text-align: center;
    text-transform: uppercase;
  }
}
@media (min-width: 768px) and (max-width: 767px) {
  .navigation__link--button {
    margin-top: 0.5rem;
    padding: 1.5rem 2rem;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .navigation__link--button {
    margin-top: 0;
    padding: 1rem 2.5rem;
  }
}
@media (min-width: 768px) {
  .navigation__link--button:hover {
    background-color: #2b272f;
    color: #fafafa;
  }
}

.intro {
  background-color: #2d2640;
  position: relative;
}
.intro .container {
  width: 100%;
}
@media (min-width: 768px) {
  .intro .container {
    width: 95%;
  }
}
@media (min-width: 768px) {
  .intro__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-flow: dense;
  }
}
.intro__img {
  grid-column: 2/3;
  position: relative;
}
.intro__image {
  width: 100%;
}
@media (min-width: 768px) {
  .intro__image {
    width: unset;
    max-width: 100%;
    position: absolute;
    right: 0;
    top: 10rem;
  }
}
.intro__info {
  padding: 10rem 1rem;
  color: #fafafa;
  text-align: center;
  margin-right: 5rem;
  margin: 0 auto;
  z-index: 0;
}
@media (max-width: 767px) {
  .intro__info {
    position: relative;
  }
}
@media (min-width: 768px) {
  .intro__info {
    padding: 10rem 0;
    text-align: left;
    grid-column: 1/2;
  }
}
.intro__heading {
  font-size: 5rem;
  font-family: "DM Serif Display", serif;
  font-weight: 400;
  line-height: 1;
}
@media (min-width: 768px) {
  .intro__heading {
    font-size: 5.5rem;
    padding-bottom: 1rem;
  }
  .intro__heading::before {
    content: "";
    display: block;
    height: 0.1rem;
    width: 15rem;
    background-color: #fafafa;
    margin-bottom: 8rem;
  }
}
@media (min-width: 1440px) {
  .intro__heading {
    font-size: 7rem;
  }
}
.intro__heading span {
  display: block;
}
.intro__description {
  font-size: 1.8rem;
  padding-bottom: 2rem;
}
.intro__btn {
  display: block;
  font-family: "Karla", sans-serif;
  font-size: 1.8rem;
  width: 16rem;
  margin: 0 auto;
  border: 0.1rem solid #fafafa;
  color: #fafafa;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .intro__btn {
    margin-top: 0.5rem;
    padding: 1.5rem 2rem;
  }
}
@media (min-width: 768px) {
  .intro__btn {
    margin-top: 0;
    padding: 1rem 2.5rem;
  }
}
.intro__btn:hover {
  background-color: #fafafa;
  color: #2d2640;
}
@media (min-width: 768px) {
  .intro__btn {
    margin: 0;
  }
}
.intro__pattern {
  position: absolute;
  z-index: -1;
}
.intro__pattern--left {
  top: 0;
  left: 0;
}
@media (min-width: 768px) {
  .intro__pattern--left {
    top: unset;
    bottom: -32rem;
  }
}
.intro__pattern--right {
  bottom: -18rem;
  right: 0;
}
@media (min-width: 768px) {
  .intro__pattern--right {
    bottom: unset;
    top: 0;
  }
}

.different {
  padding: 15rem 0;
  text-align: center;
}
@media (min-width: 768px) {
  .different {
    text-align: left;
    padding: 27rem 0 15rem 0;
  }
}
.different__heading {
  font-size: 5rem;
  margin-bottom: 2rem;
}
.different__heading::before {
  content: "";
  display: block;
  height: 0.2rem;
  width: 15rem;
  margin: 0 auto;
  margin-bottom: 5rem;
  background-color: #c6bfd9;
}
@media (min-width: 768px) {
  .different__heading::before {
    width: 13rem;
    margin: 0;
    margin-bottom: 6rem;
  }
}
@media (min-width: 768px) {
  .different__heading {
    font-size: 5.5rem;
  }
}
@media (min-width: 768px) {
  .different__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
  }
}

.item__image {
  margin: 0 auto;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .item__image {
    margin: 0;
  }
}
.item__title {
  font-family: "DM Serif Display", serif;
  font-weight: 700;
  font-size: 2.5rem;
  margin-bottom: 1rem;
  line-height: 1;
  min-height: 3rem;
}
.item__text {
  color: #837d87;
}

.banner {
  color: #fafafa;
  text-align: center;
  padding: 7rem 0;
  margin-bottom: 15rem;
  background-image: url("../img/bg-pattern-how-we-work-mobile.svg"), linear-gradient(to bottom, #2d2640 0 100%);
  background-repeat: no-repeat;
  background-position: top right;
  background-size: contain;
}
@media (min-width: 768px) {
  .banner {
    background-image: url("../img/bg-pattern-how-we-work-desktop.svg"), linear-gradient(to bottom, #2d2640 0 100%);
    background-repeat: no-repeat;
    background-position: top right;
    background-size: contain;
    padding: 7rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 1440px) {
  .banner {
    padding: 7rem;
  }
}
.banner__heading {
  font-size: 4.5rem;
  font-weight: 400;
  margin-bottom: 4rem;
  line-height: 1;
}
@media (min-width: 768px) {
  .banner__heading {
    text-align: left;
    margin-bottom: 0;
  }
}
.banner__heading span {
  display: block;
}
.banner__btn {
  width: 20rem;
  height: 4rem;
  font-size: 1.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  font-family: "Karla", sans-serif;
  border: 0.1rem solid #fafafa;
  color: #fafafa;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .banner__btn {
    margin-top: 0.5rem;
    padding: 1.5rem 2rem;
  }
}
@media (min-width: 768px) {
  .banner__btn {
    margin-top: 0;
    padding: 1rem 2.5rem;
  }
}
.banner__btn:hover {
  background-color: #fafafa;
  color: #2d2640;
}
@media (min-width: 768px) {
  .banner__btn {
    margin: 0;
  }
}

.footer {
  padding: 5rem 0;
  text-align: center;
  background-repeat: no-repeat;
  background-position: top left;
  background-size: contain;
  background-image: url("../img/bg-pattern-footer-mobile.svg"), linear-gradient(to bottom, #fafafa 0% 100%);
}
@media (min-width: 768px) {
  .footer {
    padding: 5rem 0 3rem 0;
    background-size: unset;
    background-image: url("../img/bg-pattern-footer-desktop.svg"), linear-gradient(to bottom, #fafafa 0% 100%);
  }
}
.footer__top {
  border-bottom: 0.1rem solid #b5b2b8;
  padding: 6rem 0 4rem 0;
}
@media (min-width: 768px) {
  .footer__top {
    width: unset;
    display: flex;
    justify-content: space-between;
    padding: 1rem 0;
  }
}
.footer__logo {
  width: 15rem;
  margin: 0 auto;
  padding-bottom: 3rem;
}
@media (min-width: 768px) {
  .footer__logo {
    margin: 0;
  }
}
.footer__social img:hover {
  filter: invert(75%) sepia(70%) saturate(300%) hue-rotate(200deg) brightness(40%) contrast(100%);
}
.footer__networks {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.footer__bot {
  padding: 4rem 0 2rem 0;
  text-transform: uppercase;
  font-size: 1.8rem;
  font-family: "Karla", sans-serif;
}
@media (min-width: 768px) {
  .footer__bot {
    padding: 4rem 0 1rem 0;
  }
}
.footer__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .footer__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__title {
  color: #837d87;
  margin-bottom: 2.5rem;
}
@media (min-width: 768px) {
  .footer__title {
    text-align: left;
  }
}
.footer__links {
  display: flex;
  flex-direction: column;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .footer__links {
    align-items: flex-start;
  }
}
.footer__link {
  margin: 1rem 0;
  color: #2d2640;
  text-decoration: none;
}
.footer__link:hover {
  text-decoration: underline;
}